import React, { useState } from 'react';
import { 
  Box, 
  Typography, 
  TextField, 
  Button, 
  Paper, 
  List, 
  ListItem, 
  ListItemIcon, 
  Select,
  MenuItem,
  FormControl,
  InputLabel
} from '@mui/material';
import { 
  Person, 
  Lock, 
  Email, 
  Badge, 
  Phone, 
  CalendarToday, 
  Work, 
  PieChart
} from '@mui/icons-material';
import { styled } from '@mui/material/styles';

const StyledTextField = styled(TextField)({
  marginBottom: '16px',
});

const StyledFormControl = styled(FormControl)({
  marginBottom: '16px',
  minWidth: 120,
  width: '100%',
});

const JoinDreamer = () => {
  const [job, setJob] = useState('');
  const [interest, setInterest] = useState('');

  const handleJobChange = (event) => {
    setJob(event.target.value);
  };

  const handleInterestChange = (event) => {
    setInterest(event.target.value);
  };

  return (
    <Box sx={{ maxWidth: 400, margin: 'auto', mt: 4 }}>
      <Typography variant="h4" component="h1" sx={{ color: '#1976d2', mb: 2, fontWeight:"bold" }}>
        InvestBridge
      </Typography>
      
      <Paper elevation={3} sx={{ p: 3, clear: 'both', mt:4 }}>
        <List disablePadding>
          <ListItem disablePadding>
            <ListItemIcon><Person /></ListItemIcon>
            <StyledTextField fullWidth label="" variant="standard" />
          </ListItem>
          <ListItem disablePadding>
            <ListItemIcon><Lock /></ListItemIcon>
            <StyledTextField fullWidth label="" type="password" variant="standard" />
          </ListItem>
          <ListItem disablePadding>
            <ListItemIcon><Email /></ListItemIcon>
            <StyledTextField fullWidth label="" variant="standard" />
          </ListItem>
        </List>
      </Paper>
      
      <Paper elevation={3} sx={{ p: 3, mt: 2 }}>
        <List disablePadding>
          <ListItem disablePadding>
            <ListItemIcon><Badge /></ListItemIcon>
            <StyledTextField fullWidth label="" variant="standard" />
          </ListItem>
          <ListItem disablePadding>
            <ListItemIcon><Phone /></ListItemIcon>
            <StyledTextField fullWidth label="" variant="standard" />
          </ListItem>
          <ListItem disablePadding>
            <ListItemIcon><CalendarToday /></ListItemIcon>
            <StyledTextField fullWidth label="" variant="standard" />
          </ListItem>
          <ListItem disablePadding>
            <ListItemIcon><Work /></ListItemIcon>
            <StyledFormControl variant="standard">
              <InputLabel></InputLabel>
              <Select
                value={job}
                onChange={handleJobChange}
                label=""
              >
                <MenuItem value="employee"></MenuItem>
                <MenuItem value="student"></MenuItem>
                <MenuItem value="unemployed"></MenuItem>
                <MenuItem value="entrepreneur"></MenuItem>
              </Select>
            </StyledFormControl>
          </ListItem>
          <ListItem disablePadding>
            <ListItemIcon><PieChart /></ListItemIcon>
            <StyledFormControl variant="standard">
              <InputLabel></InputLabel>
              <Select
                value={interest}
                onChange={handleInterestChange}
                label=""
              >
                <MenuItem value="bigData"></MenuItem>
                <MenuItem value="ai"></MenuItem>
                <MenuItem value="game"></MenuItem>
                <MenuItem value="healthcare">-</MenuItem>
                <MenuItem value="energyChemistry">-</MenuItem>
                <MenuItem value="finance"></MenuItem>
              </Select>
            </StyledFormControl>
          </ListItem>
        </List>
      </Paper>
      
      <Button 
        variant="contained" 
        color="primary" 
        fullWidth 
        sx={{ mt: 2 }}
      >
        
      </Button>
    </Box>
  );
};

export default JoinDreamer;